import React from 'react'
import TopSlider from './TopSlider'
import "../css/top.css"
import { Scroll } from "./ScrollAnimation";
const TopPage = () => {
    return (
        <div className='TopField'>
            <div className="Slider">
                <TopSlider />
                <div className="Message">
                    <Scroll>
                        <h1></h1>
                        <p></p>
                        <p>21</p>
                    </Scroll>
                </div>
            </div>
        </div>
    )
}

export default TopPage
